.fixed-header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  z-index: 1000;
}

.content {
  margin-top: 120px; /* Adjust this value based on the header height */
}

.addTask{
  padding: 5px;
  font-size: 32px;
  height: 40%; /* Increase the height */
  width: 80%; /* Increase the width */
  margin-bottom: 50px; /* Add margin at bottom */
  margin-left: 10px;
  box-shadow: 2px 2px 8px rgba(1, 41, 11, 0.6);
}
.dueDate{
color: orange;
}
.button {
  padding: 2px 2px;
  margin-left: 2px;
  margin-bottom: 15px;
  border: 1px solid black;
  background-color: #19600c;
  color: rgb(243, 244, 245);
  box-shadow: 6px 6px 8px rgba(34, 33, 33, 0.4);
  cursor: pointer;
  font-size: 28px;
  border-radius: 5px;
}
.button:hover {
  opacity: 0.8;
}

/* Clicked button animation */
.clicked {
  transform: scale(0.8);
}

.large-font {
  font-size: 40px; /* Adjust the size as needed */
}
.button_selected {
  padding: 2px 2px;
  margin-left: 10px;
  border: 1px solid black;
  background-color: rgb(28, 159, 59);
  color: white;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
  cursor: pointer;
  font-size: 20px;
}

.button_delete_selected {
  padding: 1px 1px;
  margin-left: 10px;
  background-color: rgb(244, 107, 107);
  color: white;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
  cursor: pointer;
  font-size: 18px;
}


ul {
  list-style-type: none;
  padding: 0;
  margin-right: 0;
  width: 100%; 
  font: 24px Arial, sans-serif;
}

li {
  margin-bottom: 1px;
  width: 100%; /* Add this line */
}


.completed {
  text-decoration: line-through;
  color: gray;
}

form {
  position: fixed;
  bottom: 0;
  left: 0;
  margin-bottom: 5px; /* Move the text box up 10 pixels */
}
.inputtextbox
{
  padding: 5px;
  font-size: 16px;
  height: 30%; /* Increase the height */
  width: 55%; /* Increase the width */
  margin-left: 5px;
  border: 1px solid black;
}
.addbutton {
  background-color:rgb(16, 130, 16);
  color: #fff;
  cursor: pointer;
  margin-left: 10px;
  box-shadow: 2px 2px 8px rgba(1, 55, 20, 0.8);
  width: 10%; /* Increase the width */
  height: 48px; /* Increase the height */
}

.signoutbutton {
  background-color: orange;
  border: none;
  color: #fff;
  cursor: pointer;
  margin-left: 12px;
  padding: 5px 5px;
  box-shadow: 6px 2px 8px rgba(0, 0, 0, 0.4);
  font-size: 18px; /* make the button text extremely small */
}
.textbutton {
  background-color: grey;
  border: none;
  color: #fff;
  cursor: pointer;
  margin-left: 10px;
  padding: 5px 5px;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.8);
  font-size: 18px; /* make the button text extremely small */
}

.markcompletebutton {
  background-color: darkcyan;
  border: black;
  color: white;
  cursor: pointer;
  margin-left: 5px;
  margin-right: 5px;
  padding: 1px 1px;
  box-shadow: 0px 0px 4px rgba(23, 2, 2, 0.953);
  font-size: 18px; /* make the button text extremely small */
}
.showcompletedbutton {
  background-color: rgb(239, 216, 14);
  border: none;
  color: black;
  cursor: pointer;
  margin-bottom: 110px;
  padding: 10px 10px;
  font-size: 16px; /* make the button text extremely small */
}
.textbutton:hover {
  background-color: #555;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
}
.signoutbutton:hover {
  background-color: #555;
  box-shadow: 6px 2px 8px rgba(0, 0, 0, 0.8);
}
.textinput {
  padding: 2px;
  font-size: 16px;
  height: 30%; /* Increase the height */
  width: 65%; /* Increase the width */
  margin-left: 5px;
  border: 1px solid #070707;
  box-shadow: 2px 2px 8px rgba(4, 4, 4, 0.8);
}

.signonpagebutton {
  background-color: rgb(58, 111, 58);
  border: none;
  color: #fff;
  cursor: pointer;
  margin-left: 10px;
  box-shadow: 6px 2px 8px rgba(0, 0, 0, 0.8);
  padding: 5px 5px;
  font-size: 18px; /* make the button text extremely small */
}

.signuppagebutton {
  background-color: rgb(148, 118, 9);
  border: none;
  color: #fff;
  cursor: pointer;
  margin-left: 10px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
  padding: 5px 5px;
  font-size: 18px; /* make the button text extremely small */
}

.signgooglepagebutton {
  background-color: rgb(31, 115, 211);
  border: none;
  color: #fff;
  cursor: pointer;
  margin-left: 10px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
  padding: 5px 5px;
  font-size: 18px; /* make the button text extremely small */
}
.editForm {
  position: fixed;
  padding: 10px;
  width: 60%;
  bottom: 30%;
  left: 40%;
  transform: translate(-50%, -50%);
  border: 2px solid;
  background-color: whitesmoke;
  box-shadow: 2px 4px 12px rgb(7, 7, 7);
  margin-bottom: 5px; /* Move the text box up 10 pixels */
  font-size: 18px;
}


.deduct-button {
  background-color: rgb(169, 78, 78);
  color: white;
  border: none;
  padding: 10px 20px;
  margin-right: 10px;
  cursor: pointer;
  border-radius: 5px;
}

.history {
  margin-top: 30px;
}

.history-item {
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

/* Reward button specific styles */
.reward-button {
  background-color: rgb(235, 241, 244);
  color: rgb(28, 34, 120);
  padding: 2px 2px;
  font-size: 28px;
  box-shadow: 6px 6px 8px rgba(34, 33, 33, 0.4);
}

.reward-button:hover {
  background-color: #65b1cb; /* Slightly darker on hover */
}

/* Total Score styles */
.score {
  transition: font-size 0.4s ease;
}

.score-popped {
  transform: scale(2);
  font-size: 2em;
}

.activities {
  padding: 1px;
}

.punishmentbutton {
  background-color: rgb(247, 156, 156);
  font-size: 26px;
  color: rgb(68, 8, 8);
  border: none;
  padding: 2px 2px;
  margin-right: 10px;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: 6px 6px 8px rgba(34, 33, 33, 0.4);
}

.historybutton {
  padding: 2px 2px;
  margin-left: 2px;
  margin-bottom: 1px;
  background-color: #d8ecbc;
  color: rgb(1, 17, 7);
  box-shadow: 6px 6px 8px rgba(34, 33, 33, 0.4);
  cursor: pointer;
  font-size: 24px;
}

.undobutton {
  padding: 2px 2px;
  margin-left: 2px;
  margin-bottom: 15px;
  background-color: #840404;
  color: rgb(246, 248, 250);
  box-shadow: 6px 6px 8px rgba(34, 33, 33, 0.4);
  cursor: pointer;
  font-size: 22px;
}
.edit-input {
  padding: 5px;
  font-size: 18px;
  width: 85%;
  border: 1px solid #786e6e;
  border-radius: 4px;
  margin-bottom: 5px;
}
.add-button {
  padding: 2px 2px;
  margin-left: 2px;
  margin-bottom: 15px;
  background-color: #0e5e36;
  color: rgb(246, 248, 250);
  box-shadow: 6px 6px 8px rgba(34, 33, 33, 0.4);
  cursor: pointer;
  font-size: 22px;
}

.add-button:hover {
  background-color: #45a049; /* Darker green on hover */
}